.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(238, 219, 219);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }

}
body {
  min-height: 100vh; /* Définit la hauteur minimale du body à 100% de la hauteur de la fenêtre */
  margin: 0; /* Enlève la marge par défaut */
  padding: 0; /* Enlève le padding par défaut */
  background-color: #1a1a1a; /* Exemple de couleur de fond */
}
.vignette img {
  width: 100%; /* Assure que l'image prend toute la largeur de la vignette */
  height: 200px; /* Définit une hauteur fixe pour toutes les images */
  object-fit: cover; /* Recadre l'image pour qu'elle remplisse le conteneur */
  border-radius: 8px; /* Facultatif : pour arrondir les coins des images */
}
.popup-overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(226, 23, 23, 0.7);
  z-index: 1000;
}

.popup-content {
  background-color: rgba(31, 29, 29, 0.9);
  padding: 20px;
  border-radius: 10px;
  width: 780px;
  height: 370px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.popup-btn {
  background-color: #ffa31a;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
  margin: 10px 0;
}

.leave-btn {
  background-color: #9c1111;
}
